import fscreen from 'fscreen';
import enableInlineVideo from 'iphone-inline-video';

import { PLAYER_EVENTS, MEDIA_EVENTS } from '@/data/constants';
import { parsedUA as ua } from '@/data/userAgent';
import { STYLES } from '@/ui/styles';
import { playWhenCan } from '@/utils/common';
import YoutubePlayer from '@/players/youtube';
import VPAIDPlayer from '@/players/vpaid';
import { settings } from '@/data/settings';

import { Background, EVENTS as BACKGROUND_EVENTS } from './background';
import { IconsContainer, EVENTS as ICON_EVENTS } from './iconsContainer';
import { Overlay, EVENTS as OVERLAY_EVENTS } from './overlay';
import { SkipButton, EVENTS as SKIP_BUTTON_EVENTS } from './skipButton';
import { AdultDialog, EVENTS as ADULT_DIALOG_EVENTS } from './adultDialog';
import { ControlsBar, EVENTS as CONTROLS_BAR_EVENTS } from './controlsBar';
import { ShareButton, EVENTS as SHARE_BUTTON_EVENTS } from './shareButton';
import { ShareDialog, EVENTS as SHARE_DIALOG_EVENTS } from './shareDialog';
import { showContextMenuAt } from './contextMenu';
import { isElementPartiallyInViewport } from './utils';
import { ViewabilityObserver, EVENTS as VIEWABILITY_EVENTS } from '../utils/viewability';

const CHECK_PLAYER_TIMEOUT = 5000;

export default class UIManager {
    constructor(wap) {
        this.wap = wap;
        this.randomId = Math.random();
        this._volume = 1;

        // root element
        this.background = new Background();
        // container for video players
        this.mediaSpan = null;
        // current video player
        this.videoElement = null;

        // share button and hideable share panel
        this.shareButton = new ShareButton();
        this.shareDialog = new ShareDialog(false);

        this.videoPoster = null;
        this.loadingIndicator = null;
        this.skipButton = null;
        this.closeButton = null;
        this.nonLinear = null;
        this.buttonStart = null;

        // icons
        this.iconsContainer = new IconsContainer();

        // controls frame elements
        this.controlsBar = new ControlsBar();
        this.hideControlsTimer = null;

        // viewability observer
        this.viewability = null;

        this.onThisPauseEvent = this.onPauseEvent.bind(this);
        this.onPlayFinished = wap.onPlayFinished.bind(wap);
        this.onVideoError = wap.onVideoError.bind(wap);
        this.onThisPlayEvent = this.onPlayEvent.bind(this);
    }

    createMainUI(config) {
        if (this.videoElement || this.background.element) {
            return;
        }

        window.addEventListener('message', this.onWindowMessage.bind(this), false);
        document.addEventListener('contextmenu', this.onContextMenuRequested.bind(this), false);

        const background = this.background;
        background.create(config.getParentElement(), config.getWidth(), config.getHeight());
        background.on(BACKGROUND_EVENTS.MOUSE_OVER_PLAYER, this.onMouseOverPlayer.bind(this));
        background.on(BACKGROUND_EVENTS.MOUSE_OUT_OF_PLAYER, this.onMouseOutOfPlayer.bind(this));

        // viewability observer
        this.viewability = new ViewabilityObserver(this.background.element);
        this.viewability.on(VIEWABILITY_EVENTS.VISIBLE, () => {
            // report player info (with viewability) to server - only on DEBUG build
            if (settings.isDebug) {
                this.wap.notifyInfo('viewability');
            }

            // if allowed, we should PLAY on viewability change
            if (config.shouldRespectViewability()) {
                this.viewabilityPlay();
            }
        });
        this.viewability.on(VIEWABILITY_EVENTS.HIDDEN, () => {
            // report player info (with viewability) to server - only on DEBUG build
            if (settings.isDebug) {
                this.wap.notifyInfo('viewability');
            }

            // if allowed, we should PAUSE on viewability change
            if (config.shouldRespectViewability()) {
                this.viewabilityPause();
            }
        });

        const mediaSpan = this.mediaSpan = document.createElement('span');
        mediaSpan.className = 'alfa-media-span';

        if (ua.isIOSSmall || ua.isUCBrowser) {
            mediaSpan.classList.add(STYLES.OPACITY_HIDDEN);
        }

        background.addElement(mediaSpan);

        this.createVideoTag(config);

        if (config.getYoutubeID()) {
            if (this.wap.isAllowYoutubeWrapper()) {
                let youtube = this.youtubePlayer = new YoutubePlayer();
                youtube.includeYoutubeAPI();
                youtube.createElement(mediaSpan, config.getYoutubeID());
                this.videoMain = youtube;
                youtube.addEventListener(MEDIA_EVENTS.play, this.onPlayEvent.bind(this));
                youtube.addEventListener(MEDIA_EVENTS.timeupdate, this.onTimeupdateEvent.bind(this));
                youtube.addEventListener(MEDIA_EVENTS.durationchange, this.onDurationchangeEvent.bind(this));
                youtube.addEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
                youtube.addEventListener(MEDIA_EVENTS.error, this.onVideoError);
                youtube.addEventListener(PLAYER_EVENTS.CLICK, this.wap.onPlayerClickMain.bind(this.wap));
                youtube.addEventListener(PLAYER_EVENTS.BUFFERING, this.onBuffering.bind(this));
            } else {
                this.videoMain = null;
            }
        } else {
            this.videoMain = this.videoElement;
        }

        if (!ua.isMobile) {
            if (config.isMute() != null) {
                if (config.isMute())
                    this.setVolume(0);
                else
                    this.setVolume(1);
            } else if (!config.isVPAID()) {
                this.setVolume(settings.volume);
            }
        }

        // if adult flag is present, then show warning dialog before showing UI
        if (config.isAdult()) {
            this.showAdultDialog();  // call showUI() there, if user agrees with warning
        } else {
            this.showUI();
        }

        if (!config.isVPAID()) {
            setTimeout(() => this.onCheckCurrentPlayer(), CHECK_PLAYER_TIMEOUT);
        }
    }

    createVideoTag(config) {
        let elementReplaced = false;
        if (this.videoElement) {
            this.mediaSpan.removeChild(this.videoElement);
            elementReplaced = true;
        }

        let videoElement;
        if (config.getVideoElement()) {
            console.log('[alfa] change videoElement to config.getVideoElement()');

            videoElement = this.videoElement = config.getVideoElement();
            // set player background to full translucent, so we can see underneath player if we injected as VPAID player
            this.background.enableTranslucence();
        } else {
            console.log('[alfa] change videoElement to document.createElement("video")');

            videoElement = this.videoElement = document.createElement('video');
            videoElement.style.cssText = 'width:100%;height:100%;';

            // HINT: added for iOS - elements will now be allowed to play inline, and will not automatically enter fullscreen mode when playback begins
            videoElement.setAttribute('webkit-playsinline', '');
            videoElement.setAttribute('playsinline', '');

            // HINT: support autoplay on iOS <= 9 (iphone and ipad)
            enableInlineVideo(videoElement, { iPad: true });

            // mute video to support autoplay without user gesture on:
            //   - iOS
            //   - Android
            //   - Chrome 66+
            if (config.isMute()) {
                videoElement.setAttribute('muted', '');
                videoElement.muted = true;
            }

            this.mediaSpan.appendChild(videoElement);
            videoElement.addEventListener(MEDIA_EVENTS.canplay, () => {
                this.hideLoadingIndicator();
                if (this.videoElement.paused) {
                    this.showStartButton();
                }
            });
        }

        videoElement.addEventListener(MEDIA_EVENTS.waiting, () => this.showLoadingIndicator());
        videoElement.addEventListener(MEDIA_EVENTS.playing, () => this.hideLoadingIndicator());
        videoElement.addEventListener(MEDIA_EVENTS.loadstart, () => {
            if (videoElement.src !== '') {
                this.showLoadingIndicator();
            }
        });

        videoElement.addEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
        videoElement.addEventListener(MEDIA_EVENTS.error, this.onVideoError);

        this.HTML5Player = videoElement;

        if (elementReplaced) {
            this.listenVideoEvents();
        }
    }

    createVideoControl() {
        if (!this.background.element) {
            return;
        }

        // main controls bar
        this.controlsBar.create(this.background.element, this.wap.config);
        this.controlsBar.on(CONTROLS_BAR_EVENTS.PLAY_PAUSE_CLICK, this.onPlayPauseClick.bind(this));
        this.controlsBar.on(CONTROLS_BAR_EVENTS.VOLUME_CLICK, this.onVolumeClick.bind(this));
        this.controlsBar.on(CONTROLS_BAR_EVENTS.VOLUME_CHANGE, (percent) => {
            this.setupHideControlsTimer();

            settings.volume = percent;  // save volume to localStorage
            this.setVolume(percent);    // change volume in video element
        });
        this.controlsBar.on(CONTROLS_BAR_EVENTS.FULLSCREEN_CLICK, () => this.onFullscreenClick());
        this.controlsBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK_START, () => this.onTimeBarSeekStart());
        this.controlsBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK, (percent) => this.onTimeBarSeek(percent));
        this.controlsBar.on(CONTROLS_BAR_EVENTS.TIME_SEEK_END, () => this.onTimeBarSeekEnd());

        // fullscreen change event bindings
        if (fscreen.fullscreenEnabled) {
            fscreen.addEventListener('fullscreenchange', () => this.onFullscreenChangeEvent());
        }

        this.listenVideoEvents();

        this.setVolumeUI(this.videoElement.volume);
    }

    listenVideoEvents() {
        this.videoElement.addEventListener(MEDIA_EVENTS.play, this.onThisPlayEvent);
        this.videoElement.addEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
        this.videoElement.addEventListener(MEDIA_EVENTS.timeupdate, this.onTimeupdateEvent.bind(this));
        this.videoElement.addEventListener(MEDIA_EVENTS.progress, this.onProgressEvent.bind(this));
        this.videoElement.addEventListener(MEDIA_EVENTS.canplaythrough, this.onCanPlaythroughEvent.bind(this));
        this.videoElement.addEventListener(MEDIA_EVENTS.durationchange, this.onDurationchangeEvent.bind(this));
        this.videoElement.addEventListener(MEDIA_EVENTS.loadstart, this.onLoadstartEvent.bind(this));
    }

    isInViewport() {
        return isElementPartiallyInViewport(this.background.element);
    }

    /**
     * Is element eligible to generate click through event
     *
     * @param {Element} element
     * @returns {boolean} Is valid
     */
    isValidForClickThrough(element) {
        return element === this.videoElement
            || element === this.background.element
            || element === this.videoPoster
            || element === this.iconsContainer.element
            || element === this.mediaSpan;
    }

    seekToTime(time) {
        // 1 = HAVE_METADATA - metadata for the audio/video is ready
        if (this.videoElement.readyState < 1) {
            const readyMetadata = () => {
                this.videoElement.currentTime = time;
                this.videoElement.removeEventListener(MEDIA_EVENTS.loadedmetadata, readyMetadata);
            };
            this.videoElement.addEventListener(MEDIA_EVENTS.loadedmetadata, readyMetadata);
        } else {
            this.videoElement.currentTime = time;
        }
        this.wap.state.finishedMain = false;
    }

    showAdultDialog() {
        const dialog = new AdultDialog();
        dialog.create(this.background.element);
        dialog.on(ADULT_DIALOG_EVENTS.AGREED, () => {
            this.showUI();
            if (this.wap.config.isAutoplay()) {
                this.wap.play();
            }
        });
        dialog.on(ADULT_DIALOG_EVENTS.REJECT, () => {
            window.location = (document.referrer && document.referrer !== window.location) ? document.referrer : 'https://google.com';
        });
    }

    showUI() {
        this.createPoster();
        this.showStartButton();

        this.createSharingUI();

        this.createVideoControl();
        this.showShareButton();
        if (this.wap.config.isNoUI()) {
            this.controlsBar.hide(true);
        }
    }

    showVideoControls() {
        if (this.wap.config.isNoUI()) {
            return;
        }

        this.controlsBar.show();
        this.setupHideControlsTimer();
    }

    hideVideoControls(isInstant) {
        this.controlsBar.hide(isInstant);
    }

    isVideoControlVisible() {
        return this.controlsBar.isVisible();
    }

    setupHideControlsTimer(timeout) {
        if (ua.isMobile && this.wap.scheduler.stage.isAd() || this.wap.scheduler.stage.isBegin()) {
            return;
        }

        timeout = timeout || 2500;
        clearTimeout(this.hideControlsTimer);
        this.hideControlsTimer = setTimeout(() => {
            if (ua.isMobile && this.wap.scheduler.stage.isAd()) {
                return;
            }

            if (!this.videoElement.paused) {
                this.controlsBar.hide();
            }
        }, timeout);
    }

    showYoutubePlayer() {
        if (this.videoElement !== this.youtubePlayer) {
            this.videoElement.style.visibility = 'hidden';
            this.videoElement.style.display = 'none';
            this.videoElement.removeEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
            this.videoElement.pause();

            if (this.youtubePlayer) {
                console.log('[alfa] change videoElement to this.youtubePlayer');

                this.videoElement = this.youtubePlayer;
                // this.videoElement.style.visibility = 'visible';
                // this.videoElement.style.display = 'inline';
                this.videoElement.addEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);

                this.controlsBar.totalTime.setTime(this.videoElement.duration);
            } else {
                console.log('[alfa] change videoElement to null');

                this.videoElement = null;
            }
        }
    }

    showHTML5Player() {
        let HTML5Player = this.HTML5Player;
        let videoElement = this.videoElement;
        if (videoElement !== HTML5Player) {
            videoElement.style.visibility = 'hidden';
            videoElement.style.display = 'none';
            videoElement.removeEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
            videoElement.removeEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
            videoElement.removeEventListener(MEDIA_EVENTS.play, this.onThisPlayEvent);
            videoElement.removeEventListener(MEDIA_EVENTS.error, this.onVideoError);
            if (!videoElement.paused)
                videoElement.pause();

            console.log('[alfa] change videoElement to HTML5Player');

            this.videoElement = HTML5Player;
            HTML5Player.style.visibility = 'visible';
            HTML5Player.style.display = 'inline';
            HTML5Player.style.width = '100%';
            HTML5Player.style.height = '100%';

            HTML5Player.addEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
            HTML5Player.addEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
            HTML5Player.addEventListener(MEDIA_EVENTS.play, this.onThisPlayEvent);
            HTML5Player.addEventListener(MEDIA_EVENTS.error, this.onVideoError);
            if (this.youtubePlayer)
                this.youtubePlayer.style.visibility = 'hidden';
        }
    }

    showVPAIDPlayer() {
        let vpaidPlayer = this.VPAIDPlayer;

        if (!vpaidPlayer) {
            if (ua.isMobile)
                this.videoElement.load();
            vpaidPlayer = this.VPAIDPlayer = new VPAIDPlayer(this.mediaSpan, this.HTML5Player, this.wap);
            vpaidPlayer.addEventListener(MEDIA_EVENTS.play, this.onThisPlayEvent);
            vpaidPlayer.addEventListener(MEDIA_EVENTS.timeupdate, this.onTimeupdateEvent.bind(this));
            vpaidPlayer.addEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
            vpaidPlayer.addEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
            vpaidPlayer.addEventListener(MEDIA_EVENTS.durationchange, this.onDurationchangeEvent.bind(this));
            vpaidPlayer.addEventListener(MEDIA_EVENTS.error, this.onVideoError);

            vpaidPlayer.addEventListener(PLAYER_EVENTS.AD_CLICK_THROUGH, this.wap.onClickThrough.bind(this.wap));
            vpaidPlayer.addEventListener(PLAYER_EVENTS.AD_CLICK_THROUGH, () => {
                this.wap.emit(PLAYER_EVENTS.AD_CLICK_THROUGH);
            });
        }

        if (this.videoElement !== this.VPAIDPlayer) {
            this.HTML5Player.style.visibility = 'visible';
            this.HTML5Player.style.display = 'inline';
            this.HTML5Player.removeEventListener(MEDIA_EVENTS.pause, this.onThisPauseEvent);
            this.HTML5Player.removeEventListener(MEDIA_EVENTS.ended, this.onPlayFinished);
            this.HTML5Player.removeEventListener(MEDIA_EVENTS.play, this.onThisPlayEvent);
            this.HTML5Player.removeEventListener(MEDIA_EVENTS.error, this.onVideoError);
            this.HTML5Player.pause();

            this.hidePoster();

            console.log('[alfa] change videoElement to this.VPAIDPlayer');

            this.videoElement = this.VPAIDPlayer;
            this.videoElement.style.visibility = 'visible';
            this.videoElement.style.display = 'inline';
        }
    }

    showShareDialog(shareUrl) {
        this.shareButton.hide();
        this.shareDialog.show(shareUrl);
    }

    morphToAdsLayout() {
        this.hideShareButton();
        this.controlsBar.morphToAdsLayout();
        this.onTimeupdateEvent();
    }

    morphToWaitingAdsLayout() {
        this.morphToAdsLayout();
        this.showLoadingIndicator();

        this.controlsBar.morphToWaitingAdsLayout();
    }

    morphToNormalLayout() {
        this.controlsBar.morphToNormalLayout();
    }

    showCloseButton() {
        if (!this.closeButton) {
            let closeButton = this.closeButton = document.createElement('div');
            closeButton.className = 'alfa-button-close';
            closeButton.innerHTML = '&#xd7;';
            this.background.addElement(closeButton);
            closeButton.addEventListener('click', this.wap.onCloseClick.bind(this.wap));
        }
    }

    hideCloseButton() {
        if (this.closeButton) {
            this.background.removeElement(this.closeButton);
            this.closeButton = null;
        }
    }

    showLoadingIndicator() {
        if (this.wap.config.isAndroidSDK())
            return;

        if (!this.loadingIndicator) {
            this.loadingIndicator = document.createElement('div');
            this.loadingIndicator.className = STYLES.LOADING_INDICATOR;
            if (this.wap.config.isVPAID()) {
                this.loadingIndicator.classList.add(STYLES.LOADING_INDICATOR_VPAID);
            }
            this.background.addElement(this.loadingIndicator);
        }
    }

    hideLoadingIndicator() {
        if (this.loadingIndicator) {
            this.background.removeElement(this.loadingIndicator);
            this.loadingIndicator = null;
        }
    }

    showSkipButton(skipOffset) {
        if (this.background.element && !this.skipButton) {
            // create SkipButton
            this.skipButton = new SkipButton(skipOffset);
            this.skipButton.on(SKIP_BUTTON_EVENTS.CLICK, () => {
                this.wap.onSkipAdClick();
            });
            this.skipButton.create(this.background.element);
        }
    }

    hideSkipButton() {
        if (this.skipButton) {
            this.skipButton.remove();
            this.skipButton = null;
        }
    }

    showError(text) {
        this.hideLoadingIndicator();
        this.showMessage(text);
    }

    showMessage(text) {
        if (this.messageDiv) {
            this.hideMessage();
        }
        this.messageDiv = document.createElement('div');
        this.messageDiv.className = 'alfa-bg-message';
        let span = document.createElement('span');
        span.innerHTML = text;
        this.messageDiv.appendChild(span);
        this.background.addElement(this.messageDiv);
    }

    hideMessage() {
        if (this.messageDiv) {
            this.background.removeElement(this.messageDiv);
            this.messageDiv = null;
        }
    }

    showStartButton() {
        if (this.controlsBar.timeBar.isSeeking())
            return;

        this.hideRestartButton();
        this.hideEndingScreen();
        this.hideLoadingIndicator();

        if (!this.buttonStart) {
            this.buttonStart = document.createElement('div');
            this.buttonStart.className = 'alfa-button-start';
            this.buttonStart.addEventListener(ua.isMobile ? 'touchend' : 'click', this.onPlayClick.bind(this), false);
            this.background.addElement(this.buttonStart);

            const bgSize = this.background.size;
            if (bgSize.width < 210 || bgSize.height < 210) {
                this.buttonStart.style.width = '72px';
                this.buttonStart.style.height = '72px';
                this.buttonStart.style.marginLeft = '-36px';
            }
        }
    }

    hideStartButton() {
        if (this.buttonStart) {
            this.buttonStart.parentNode.removeChild(this.buttonStart);
            this.buttonStart = null;
        }
    }

    showNonLinear(nonLinearData) {
        if (!this.nonLinear) {
            if (this.background.element) {
                this.nonLinear = new Overlay(nonLinearData);
                this.nonLinear.on(OVERLAY_EVENTS.VIEW, () => {
                    this.wap.onNonLinearView();
                });
                this.nonLinear.on(OVERLAY_EVENTS.CLOSE, () => {
                    this.hideNonLinear();
                    this.wap.onNonLinearClose();
                });
                this.nonLinear.on(OVERLAY_EVENTS.CONTENT_CLICK, (info) => {
                    this.wap.onNonLinearContentClick(info);
                });
                this.nonLinear.on(OVERLAY_EVENTS.VAST_ERROR, (error) => {
                    this.wap.notifyVastError(error);
                });
                this.nonLinear.create(this.background.element);
            }
        } else {
            this.nonLinear.show();
        }
    }

    hideNonLinear() {
        if (this.nonLinear) {
            this.nonLinear.remove();
            this.nonLinear = null;
        }
    }

    createSharingUI() {
        // create only if allowed
        if (this.wap.config.isNoSharingUI()) {
            return;
        }

        this.shareButton.create(this.background.element);
        this.shareButton.on(SHARE_BUTTON_EVENTS.CLICK, () => {
            if (this.videoElement.currentTime === this.videoElement.duration)
                return;
            this.wap.pause();
            this.wap.shareCurrentVideo();
        });

        this.shareDialog.create(this.background.element);
        this.shareDialog.on(SHARE_DIALOG_EVENTS.DONE, () => {
            this.showShareButton();
        });
    }

    showShareButton() {
        if (this.wap.scheduler.stage.isAd() || this.wap.config.isVPAID() || this.wap.config.isAdOnly())
            return;

        if (this.videoElement.currentTime >= this.videoElement.duration && !isNaN(this.videoElement.duration))
            return;

        if (!this.wap.config.getVideoFile())
            return;

        if (!this.videoElement.error && !this.shareDialog.isVisible()) {
            this.shareButton.show();
        }
    }

    hideShareButton() {
        this.shareButton.hide();
        this.shareDialog.hide();
    }

    showRestartButton(parent) {
        if (this.controlsBar.timeBar.isSeeking()) {
            return;
        }

        parent = parent || this.background.element;
        this.hideStartButton();

        if (!this.restartButton) {
            this.restartButton = document.createElement('div');
            this.restartButton.className = 'alfa-button-restart';
            parent.appendChild(this.restartButton);

            this.restartButton.addEventListener('click', this.doRestart.bind(this));
        }
        return this.restartButton;
    }

    hideRestartButton() {
        if (this.restartButton !== void(0)) {
            this.restartButton.parentNode.removeChild(this.restartButton);
            this.restartButton = void(0);
        }
    }

    doRestart() {
        if (!this.wap.isAllowYoutubeWrapper() && this.wap.config.getYoutubeID()) {
            window.location = this.wap.config.getVideoFile();
            return;
        }

        if (!ua.isIOS || ua.isIOSSmall) {
            this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
        }

        // if there was an video error
        if (this.videoMain.error != null) {
            let curTime = this.videoMain.currentTime;
            let src = this.videoMain.src;
            this.videoMain.src = '';
            this.videoMain.src = src;
            this.videoMain.currentTime = curTime;
        } else {
            this.videoMain.currentTime = 0;
        }

        this.wap.state.finishedMain = false;
        this.wap.mainVideoTime = 0;
        this.videoElementPlay();
        this.showShareButton();
    }

    showEndingScreen() {
        console.log('[alfa] showEndingScreen');
        this.hideStartButton();
        this.hideShareButton();

        if (!ua.isIOS || ua.isIOSSmall) {
            this.mediaSpan.classList.add(STYLES.OPACITY_HIDDEN);
        }
        this.showPoster();
        if (this.endingScreen === void(0)) {
            this.endingScreen = document.createElement('div');
            this.endingScreen.className = 'alfa-ending-screen';

            // if sharing UI is allowed in config
            if (!this.wap.config.isNoSharingUI()) {
                const shareDiv = document.createElement('div');
                shareDiv.className = 'alfa-share-div';
                this.endingScreen.appendChild(shareDiv);

                const sharePanel = new ShareDialog(true);
                sharePanel.create(shareDiv);
                sharePanel.show(this.wap.config.getShareUrl());
            }

            // show restart button
            let restartButton = this.showRestartButton(this.endingScreen);
            restartButton.classList.add('alfa-small-restart');

            const bgSize = this.background.size;
            if (bgSize.width < 320 || bgSize.height < 240) {
                let w = bgSize.width - 20;
                let h = Math.round(w * 3 / 5);
                if (h > bgSize.height - 60) {
                    h = bgSize.height - 50;
                    w = Math.round(h * 5 / 3);
                }
                this.endingScreen.style.width = w + 'px';
                this.endingScreen.style.height = h + 'px';
            }
            this.background.addElement(this.endingScreen);
        }

        this.controlsBar.timeBar.setPlayPosition(100);
    }

    hideEndingScreen() {
        if (this.endingScreen !== void(0)) {
            this.endingScreen.parentNode.removeChild(this.endingScreen);
            this.endingScreen = void(0);
        }
    }

    showIcons() {
        // container element for VAST icons
        this.iconsContainer.create(this.background.element);
        this.iconsContainer.on(ICON_EVENTS.ICON_VIEW, (icon) => {
            this.wap.onIconView(icon);
        });
        this.iconsContainer.on(ICON_EVENTS.ICON_CLICK, (icon) => {
            this.wap.onIconClick(icon);
        });
    }

    hideIcons() {
        this.iconsContainer.off(ICON_EVENTS.ICON_VIEW);
        this.iconsContainer.off(ICON_EVENTS.ICON_CLICK);
        this.iconsContainer.remove();
    }

    resetTimeAndSeek() {
        this.controlsBar.totalTime.setTime(0);
        this.controlsBar.currentTime.setTime(0);
        this.controlsBar.timeBar.setPosition(0);
    }

    videoElementPlay() {
        this.hideMessage();
        this.hideEndingScreen();
        this.hideRestartButton();
        this.hideEndingScreen();

        if (this.wap.config.isVPAID()) {
            this.controlsBar.showPauseButton();
        }

        // run 'play' when video player is ready
        playWhenCan(this.videoElement);
    }

    disablePlay() {
        this.controlsBar.off(CONTROLS_BAR_EVENTS.PLAY_PAUSE_CLICK);
    }

    calculateBufferWidth() {
        if (this.wap.config.isVPAID())
            return;

        let len = this.videoElement.buffered.length;

        if (len > 0) {
            let lastSec = this.videoElement.buffered.end(len - 1);
            let duration = this.videoElement.duration;
            let percent = (lastSec / duration * 100);
            if (percent > this.controlsBar.timeBar.getBufferedPosition()) {
                this.controlsBar.timeBar.setBufferedPosition(percent);
            }
        } else {
            this.controlsBar.timeBar.setBufferedPosition(0);
        }
    }

    setVolume(vol) {
        this._volume = vol;
        if (ua.isIOS || ua.isUCBrowser) {
            return;
        }

        // remove MUTED attribute on element if video is not muted anymore
        if (vol > 0 && (this.videoElement.muted || this.videoElement.hasAttribute && this.videoElement.hasAttribute('muted'))) {
            this.videoElement.removeAttribute('muted');
            this.videoElement.muted = false;
        }

        if (this.videoElement.volume > vol && vol === 0) {
            this.wap.onMute();
        } else if (this.videoElement.volume === 0 && vol > 0) {
            this.wap.onUnmute();
        }
        //this.videoElement.volume = vol;
        this.HTML5Player.volume = vol;
        if (this.youtubePlayer)
            this.youtubePlayer.volume = vol;
        if (this.VPAIDPlayer)
            this.VPAIDPlayer.volume = vol;

        this.wap.emit(MEDIA_EVENTS.volumechange, vol);
        this.setVolumeUI(vol);
    }

    setVolumeUI(vol) {
        this._volume = vol;
        this.controlsBar.updateVolume(vol);
    }

    getVolume() {
        return this.videoElement.volume;
    }

    createPoster() {
        if (this.videoPoster) {
            return;
        }

        const posterUrl = this.wap.config.getPosterUrl();
        if (posterUrl) {
            this.videoPoster = document.createElement('div');
            this.videoPoster.className = 'alfa-video-poster';
            this.videoPoster.style.backgroundImage = 'url(' + posterUrl + ')';
            this.background.addElement(this.videoPoster);
        }
    }

    showPoster() {
        if (this.videoPoster) {
            this.videoPoster.style.visibility = 'visible';
        }
    }

    hidePoster() {
        if (this.videoPoster) {
            // this.background.removeElement(this.videoPoster);
            // this.videoPoster = null;
            this.videoPoster.style.visibility = 'hidden';
        }
    }

    isFullscreen() {
        return fscreen.fullscreenElement || this.background.isFullscreen();
    }

    enterFullscreen() {
        if (fscreen.fullscreenEnabled) {
            fscreen.requestFullscreen(ua.isIOS ? this.videoElement : this.background.element);
        } else {
            this.background.enterFullscreen();
        }

        this.wap.onEnterFullscreen();
    }

    exitFullscreen() {
        if (fscreen.fullscreenEnabled) {
            fscreen.exitFullscreen();
        } else {
            this.background.exitFullscreen();
        }

        this.wap.onExitFullscreen();
    }

    onFullscreenChangeEvent() {
        if (this.background.isFullscreen()) {
            this.background.exitFullscreen();
        }

        if (this.VPAIDPlayer)
            this.VPAIDPlayer.onParentResize();
    }

    onFullscreenClick() {
        this.setupHideControlsTimer();

        if (!this.isFullscreen()) {
            this.enterFullscreen();
        } else {
            this.exitFullscreen();
        }
    }

    viewabilityPause() {
        if (this.wap.scheduler.stage.isAd() && !this.wap.isPaused()) {
            this.__wasPausedByViewability = true;
            this.wap.pause();
        }
    }

    viewabilityPlay() {
        if (this.__wasPausedByViewability && this.wap.scheduler.stage.isAd() && this.wap.isPaused()) {
            this.__wasPausedByViewability = false;

            if ((this.videoElement.currentTime < this.videoElement.duration) && !this.wap.state.initial && !isNaN(this.videoElement.duration)) {
                this.wap.play();
            }
        }
    }

    get isSeekingNow() {
        return this.controlsBar.timeBar.isSeeking();
    }
}

// ************************************** Events ***********************************************
UIManager.prototype.onContextMenuRequested = function (e) {
    // if menu requested on our UI
    if (!this.background.isInBounds(e.toElement)) {
        return;
    }
    e.preventDefault();

    const items = [
        {
            id: 'about_player',
            text: 'About Web Alfa player',
            href: 'http://www.alfaplayer.com/' + (this.wap.config.isVPAID() ? 'share/' : '')
        }
    ];
    showContextMenuAt(e.x, e.y, items, this.background.element);
};

UIManager.prototype.onCheckCurrentPlayer = function () {
    const id = settings.currentPlay || this.randomId;
    if (id !== this.randomId && this.videoElement && !this.videoElement.paused) {
        this.videoElement.pause();
    }
    setTimeout(this.onCheckCurrentPlayer.bind(this), CHECK_PLAYER_TIMEOUT);
};

UIManager.prototype.onBuffering = function () {
    console.log('[alfa] OnBuffering');
    this.hidePoster();
    this.controlsBar.showPauseButton();

    this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
};

UIManager.prototype.onWindowMessage = function (e) {
    let data = e.data + '';
    if (data.indexOf('ALFAPLAYER_PLAY_EVENT') === 0) {
        if (parseFloat(data.split(':')[1]) !== this.randomId) {
            this.wap.pause();
        }
    }
};

UIManager.prototype.onVolumeClick = function () {
    console.log('[alfa] on volume button click');

    if (this._volume > 0) {
        // mute
        this._previousVolume = this._volume;
        settings.volume = 0;
        this.setVolume(0);
    } else if (this._previousVolume > 0) {
        // unmute
        settings.volume = this._previousVolume;
        this.setVolume(this._previousVolume);
    } else {
        settings.volume = 1;
        this.setVolume(1);
    }
};

UIManager.prototype.onPlayClick = function (e) {
    e.stopPropagation();
    e.preventDefault();

    if (!this.wap.config.isVPAID()) {
        settings.currentPlay = this.randomId;
    }

    this.wap.play();
};

UIManager.prototype.onPlayPauseClick = function () {
    this.setupHideControlsTimer();

    if (this.wap.isPaused()) {
        if (!this.wap.config.isVPAID()) {
            settings.currentPlay = this.randomId;
        }

        if (!this.wap.scheduler.stage.isAd() && !(this.videoElement.currentTime < this.videoElement.duration) && !this.wap.state.initial && !isNaN(this.videoElement.duration)) {
            this.doRestart();
        } else {
            this.wap.play();
        }
    } else {
        this.wap.pause();
    }
};

UIManager.prototype.onPauseEvent = function () {
    if (this.controlsBar.timeBar.isSeeking())
        return;

    this.controlsBar.showPlayButton();
    this.showVideoControls();
    this.hideLoadingIndicator();

    if (this.wap.scheduler.stage.isBegin())
        return;

    if (!this.wap.scheduler.stage.isAd()) {
        if (this.videoElement.error !== null)
            this.showRestartButton();
        else if (this.videoElement.currentTime < this.videoElement.duration && !this.wap.state.finishedMain) {
            this.showStartButton();
            console.log('[alfa] Show start button on pause', this.videoElement.currentTime, this.videoElement.duration);
        }
    } else if (this.videoElement.currentTime < this.videoElement.duration) {
        this.showStartButton();
    }

    this.wap.onPauseEvent();
};

UIManager.prototype.onPlayEvent = function () {
    this.hideStartButton();
    this.hideEndingScreen();
    this.hideRestartButton();
    this.hidePoster();
    this.controlsBar.showPauseButton();
    this.wap.onPlayEvent();
    let mediaSpan = this.mediaSpan;
    if (mediaSpan) {
        this.mediaSpan.classList.remove(STYLES.OPACITY_HIDDEN);
    }

    if (!this.wap.config.isVPAID()) {
        settings.currentPlay = this.randomId;

        let sendplayevent = function (wnd) {
            wnd.postMessage('ALFAPLAYER_PLAY_EVENT:' + this.randomId, '*');
        }.bind(this);
        sendplayevent(window.self);
    }
};

UIManager.prototype.onDurationchangeEvent = function () {
    this.controlsBar.totalTime.setTime(this.videoElement.duration);
    this.wap.emit(MEDIA_EVENTS.durationchange, this.videoElement.duration);
};

UIManager.prototype.onLoadstartEvent = function () {
    this.controlsBar.totalTime.setTime(0);
    if (!this.controlsBar.isAdsLayout()) {
        this.controlsBar.currentTime.setTime(0);
    }
    // this.controlsBar.timeBar.setPosition(0);
};

UIManager.prototype.onTimeupdateEvent = function () {
    const duration = this.videoElement.duration;
    const curTime = this.videoElement.currentTime;

    if (ua.isIE) {
        this.hideLoadingIndicator();
    }

    // update SkipButton text on video time progress (if exists)
    if (this.skipButton) {
        this.skipButton.update(curTime);
    }

    if (this.controlsBar.isAdsLayout()) {
        // show ad icons for current time, if any
        if (this.wap.currentAd && this.wap.currentAd.hasIcons()) {
            // TODO: listen to resize of root player container, instead of resizing every second
            this.iconsContainer.resizeToVideo(this.videoElement);

            const icons = this.wap.currentAd.getIconsForTime(curTime);
            this.iconsContainer.showIcons(icons);
        }

        if (duration >= curTime) {
            const remain = duration - curTime;
            if (remain > 0)
                this.controlsBar.currentTime.setText('Ad remains:', remain);
            else
                this.controlsBar.currentTime.setText('');
        } else {
            this.controlsBar.currentTime.setText('');
        }
        return;
    }
    if (this.videoElement !== this.videoMain) {
        return;
    }

    this.controlsBar.currentTime.setTime(curTime);
    // TODO: why there is TimeBar recalculating?
    // if (curTime > 3600) {
    //      this.controlsBar.calculateSeekBarWidth();
    // }

    if (this.videoElement.style && this.videoElement.style.display) {
        this.videoElement.style.display = null;
    }

    const percent = curTime / duration * 100;
    this.controlsBar.timeBar.setPlayPosition(percent);
};

UIManager.prototype.onProgressEvent = function () {
    this.calculateBufferWidth();
};

UIManager.prototype.onCanPlaythroughEvent = function () {
    this.calculateBufferWidth();
};

UIManager.prototype.onMouseOverPlayer = function () {
    if (!ua.isMobile) {
        this.showVideoControls();
    }
};

UIManager.prototype.onMouseOutOfPlayer = function () {
    if (!ua.isMobile && !this.videoElement.paused && !isNaN(this.videoElement.duration)) {
        this.setupHideControlsTimer(300);
    }

    if (this.shareDialog.isVisible()) {
        this.shareDialog.hide();
        if (!this.wap.state.finishedMain)
            this.showShareButton();
    }
};

UIManager.prototype.onTimeBarSeekStart = function () {
    if (!this.videoElement) {
        return;
    }

    this.wasPlayingBeforeSeek = this.videoElement.paused === false;
};

UIManager.prototype.onTimeBarSeek = function (percent) {
    if (!isNaN(this.videoElement.duration)) {
        if (ua.isMobile) {
            this.setupHideControlsTimer();
        }

        // rewind current player
        // HINT: rewinding not working if "Disable Cache" is set in browser DevTools
        const newTime = this.videoElement.duration * percent / 100;
        this.videoElement.currentTime = newTime;
    }
};

UIManager.prototype.onTimeBarSeekEnd = function () {
    if (this.wasPlayingBeforeSeek && this.videoElement.currentTime < this.videoElement.duration && !isNaN(this.videoElement.duration))
        this.videoElementPlay();

    if (this.videoElement.currentTime >= this.videoElement.duration && !isNaN(this.videoElement.duration)) {
        this.wap.onPlayFinished();
        this.onPauseEvent();
    } else if (!this.wasPlayingBeforeSeek) {
        this.showStartButton();
    }
};



